*, ::before, ::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html, body {
  background-color: #7289DA;
  color: white;
  font-family: 'Whitney', 'Helvetica Neue', 'Helvetica', 'Open Sans', Arial, sans-serif;
  height: 100vh;
  margin: 0;
  overflow-x: hidden;
  width: 100vw;
}

a {
  color: white;
  text-decoration: none;
}
a:hover,
a:focus {
  color: black;
}



